@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap");

* {
    cursor: none;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "poppins", sans-serif;
}

@layer components{
  .marquee-30{
    animation: marquee 60s linear infinite;
  }
  .marquee-15{
    animation: marquee 45s linear infinite;
  }
  .marquee-10{
    animation: marquee 30s linear infinite;
  }
  .fade-edge{
      -webkit-mask: linear-gradient(
      90deg,
      transparent,
      white 5%,
      white 95%,
      transparent
    );
    mask: linear-gradient(90deg, transparent, white 20%, white 80%, transparent);
  }
}

@keyframes marquee{
  to {
    transform: translateX(calc(-50% - 0.5rem));
  }
}

.text-heading {
    font-size: clamp(2rem,5vw,6rem);
}


.blog-sidebar {
    @apply transition duration-300 transform hover:translate-y-[2px] hover:text-white;
  font-weight: 600;
  text-shadow: -2px -2px 0 #585858, 2px -2px 0 #585858, -2px 2px 0 #585858, 2px 2px 0 #585858;
}

.blog-sidebar:hover {
  text-shadow: none;
}


.text-list-item {
    font-size: clamp(1rem,2vw,1.875rem);
    line-height: clamp( 2.5rem,6vw,3.75rem);
}

#card {
    background-size: cover;
    background-image: url(./assets/images/profilePhoto.png);
}

#welcome-screen,
#about-screen, 
#contact-screen {
    background-image: url(./assets/images/bg-noise.png);
}

#technologies-screen li {
    @apply  list-item hover:list-disc hover:translate-x-10 transition-all text-pretty hover:font-[500]
}

#overlay .cell {
    @apply bg-purple
}

#overlaySecond .cell {
    @apply bg-pink

}

#heading-container {
  font-size: clamp(2rem, 9vw, 10rem);
  /* letter-spacing: clamp(-1.8rem, -1.2vw, -0.8rem); */
}

#text-container {
  font-size: clamp(0.4rem, 1.75vw, 1.5rem);
}
